*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

html {
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  scroll-behavior: smooth; }

body {
  margin: 0 auto;
  padding: 0;
  min-height: 100%;
  min-width: 320px;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  background-color: #ffffff;
  color: #000000; }

img {
  max-width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  display: block; }

input,
textarea,
select,
button {
  font: inherit; }

a {
  color: inherit;
  cursor: pointer; }

a:not([href]) {
  cursor: auto; }

button {
  cursor: pointer; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1366px;
  min-width: 320px;
  padding: 0 7.17%;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .container {
      width: 100%;
      padding: 0 3.24%; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 4.68%; } }
